<template>
    <div id="saga" class="saga">
        <div class="saga-wrapper" :class="{'fin':questionNumber === question.length}">
            <a class="saga__partners" href="http://bit.ly/2QtB5WJ" target="_blank">
                <img :src="images.saga" alt="" class="saga__img">
                <div class="saga__desc">
                    Партнерський матеріал
                </div>
            </a>
            <div class="saga__header" v-if="questionNumber<question.length">
                <div class="saga__bg"></div>
                <div class="container">
                    Тест: який район Києва вам найбільше підходить?
                </div>
            </div>
            <div class="saga__header"  :style="{backgroundImage: 'url('+images[result.img]+')'}" v-if="questionNumber === question.length">
                <div class="saga__bg"></div>
                <div class="container">
                    Ваш ідеальний район – {{result.name}}!
                </div>
            </div>
            <div class="saga__question-wrapper">
                <div class="saga__start" v-if="!start">
                    <div class="saga__startheader">
                        Печерськ, Поділ, центр чи Оболонь – який з районів Києва найбільше підходить вам по духу і стилю життя?
                        Відповідайте на запитання та отримуйте персональний результат! </div>
                    <button class="saga__nextbutton" @click="startTest" v-if="!start">Почати!
<!--                        <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd"-->
<!--                             clip-rule="evenodd">-->
<!--                            <path  d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"/>-->
<!--                        </svg>-->
                    </button>
                </div>
                <div class="saga__question question" v-if="questionNumber<question.length && start">
                    <div class="question__number"><span>{{questionNumber+1}}</span>/ 9</div>
                    <div class="question__name">
                        {{question[questionNumber].name}}
                    </div>
                    <div class="question__answer-wrapper">
                        <div class="question__answer" @click="goToNextQuestion(0)" :class="{'showad':showAdA}" v-if="!showAdB">
                           a) {{question[questionNumber].answers.a}}
                        </div>
                        <div class="question__ad" v-if="showAdA">
                            <div class="question__addesc" v-html="question[questionNumber].answers['a_ad']">
                            </div>
                            <img :src="images[question[questionNumber].answers.adimg]" alt="" class="question__adimg">

                        </div>
                        <div class="question__answer" @click="goToNextQuestion(1)" :class="{'showad':showAdB}" v-if="!showAdA">
                            б)  {{question[questionNumber].answers.b}}
                        </div>
                        <div class="question__ad" v-if="showAdB">
                            <div class="question__addesc" v-html="question[questionNumber].answers['b_ad']">
                            </div>
                            <img :src="images[question[questionNumber].answers.adimg]" alt="" class="question__adimg">

                        </div>
                        <button class="saga__nextbutton" @click="goToNextQuestion(+showAdB)" v-if="showAdA || showAdB">Продовжити тест!
                            <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd"
                                 clip-rule="evenodd">
                                <path d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"/>
                            </svg>
                        </button>
                    </div>
                </div>


                <div class="saga__result result" v-if="questionNumber === question.length">
                    <div class="social-media">
                        <span>Поділіться з друзями!</span>
                        <facebook :url=path scale="3"></facebook>
                        <telegram :url=path scale="3"></telegram>
                        <twitter :url=path scale="3"></twitter>

                    </div>
                    <div class="result__desc" v-html="result.description">
                    </div>
                    <img :src="images[result.imgad]" alt="" v-if="images[result.imgad]">


                    <button class="saga__nextbutton" @click="questionNumber = 0, start= false">Спробувати ще раз!
                    </button>

                </div>

            </div>
        </div>

    </div>


</template>
<script>
    import Quest from "../assets/questions.json";
    import Ans from "../assets/answers";
    import 'reset-css';
    import '../sass/main.scss';
    import saga from '../assets/img/saga.png';
    import berezniaki from '../assets/img/berezniaki.png';
    import nivki from '../assets/img/nivki.png';
    import novazabudova from '../assets/img/novazabudova.png';
    import obolon from '../assets/img/obolon.png';
    import parkps from '../assets/img/parkps.png';
    import pechersk from '../assets/img/pechersk.png';
    import podil from '../assets/img/podil.png';
    import pusha from '../assets/img/pusha.png';
    import rusanivka from '../assets/img/rusanivka.png';
    import uppercity from '../assets/img/upper-city.png';
    import rybalskyquest from '../assets/img/rybalskyquest.jpg';
    import o2quest from '../assets/img/o2quest.jpg';
    import sityspacequest from '../assets/img/sityspacequest.jpg';
    import bostonfin from '../assets/img/bostonfin.jpg';
    import rybalskyfin from '../assets/img/rybalskyfin.jpg';
    import sityspacefin from '../assets/img/sityspacefin.jpg';
    import vashibgtonfin from '../assets/img/vashibgtonfin.jpg';
    import o2fin from '../assets/img/o2fin.jpg';
    import {
        Facebook,
        Twitter,
        Telegram
    } from 'vue-socialmedia-share';

    let myPathToImg;
    if (document.getElementById('path')) {
        myPathToImg = document.getElementById('path').dataset.path;
    } else {
        myPathToImg = '/'
    }

    export default {
        components: {
            Facebook,
            Twitter,
            Telegram
        },
        name: 'SAGA',
        data: function () {
            return {
                questionNumber: 0,
                answerSet: [],
                result: "",
                images:{
                    nivki,
                    saga,
                    berezniaki,
                    novazabudova,
                    obolon,
                    parkps,
                    pechersk,
                    podil,
                    pusha,
                    rusanivka,
                    uppercity,
                    rybalskyquest,
                    o2quest,
                    sityspacequest,
                    bostonfin,
                    rybalskyfin,
                    sityspacefin,
                    vashibgtonfin,
                    o2fin
                },
                start: false,
                showAdA:false,
                showAdB:false,
                pathToImg: myPathToImg + 'img/' || "assets/img/",
                path: window.location.href,
            }
        },
        mounted() {
        },
        computed: {
            question() {
                return Quest.questions.map((item) => {
                    return item;
                })
            },
            answer() {
                return Ans.results.map((item) => {
                    return item;
                })
            }
        },
        methods: {
            goToNextQuestion: function (val) {
                if(this.question[this.questionNumber].answers['a_ad']   && !this.showAdA  && val === 0) {
                    this.showAdA = true;
                }
                else if(this.question[this.questionNumber].answers['b_ad']   && !this.showAdB  && val === 1){
                    this.showAdB = true;
                }
                else {
                    this.showAdA = false;
                    this.showAdB = false;
                    this.answerSet[this.questionNumber] = val;
                    this.questionNumber += 1;
                    if (this.questionNumber === this.question.length) {
                        window.scrollTo(0,0);
                        let resultArray = [];
                        resultArray.length = this.answer.length;
                        resultArray.fill(0);
                        this.answer.forEach((item, index) => {
                            [...item.code].forEach((itemCode, indexCode) => {
                                if (+itemCode === this.answerSet[indexCode]) {
                                    resultArray[index] += 1;
                                }
                            })
                        })
                        let maxEl = Math.max(...resultArray);
                        let vinners=[];
                        resultArray.forEach((resitem, resindex)=>{
                            if(resitem === maxEl){
                                vinners.push(resindex)
                            }
                        })
                        let vinnerHappy = vinners[Math.floor(Math.random()*vinners.length)];;
                        this.result = this.answer[vinnerHappy];
                    }
                }
            },
            startTest: function () {
                this.start = true;
            }
        }
    }
</script>